html,
body {
  max-width: 100%;
  overflow-x: hidden;
  font-family: "Lato", sans-serif;
  margin: 0% 5%;
}

/* HEADER */

header {
  text-align: center;
  max-width: 1006px;
  margin: auto;
}

.header-green-bar {
  height: 61px;
  background: #7abcac;
}

.header-name-bar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 586px;
  padding: 30px 0px;
  border: 4px solid black;
  text-align: center;
  margin: auto;
  margin-top: -38px;
}

.header-name-container {
  font-size: 48px;
}

.header-profesion-container {
  color: #fff;
  background-color: #000;
  padding: 0px 49px;
  line-height: 28px;
  font-size: 24px;
  margin-top: 18px;
}

/* ============================================== */

/* SECTION GENERAL */

section {
  text-align: justify;
  max-width: 991px;
}
section h3 {
  font-size: 24px;
  font-weight: bold;
  text-align: left;
  text-transform: uppercase;
}

.section-hr {
  display: flex;
  align-items: center;
  width: 100%;
  margin: 11px 0px 19px 0px;
}

.sections-hr-green-part {
  height: 10px;
  width: 72px;
  background: #7abcac;
}

.sections-hr-grey-part {
  width: 100%;
  height: 4px;
  background: #dcdcdc;
}

/* =============================== */

/* SECTION 1 ABOUT ME + SECTION 2 LINKS */

.wrapper-aboutMe-links {
  max-width: 1006px;
  margin: 72px auto 0px auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 66px;
}

/* =============================== */
/* SECTION 1 - ABOUT ME */

section.section-aboutMe {
  grid-column: 2 / 4;
  order: 2;
}

section.section-aboutMe p {
  font-size: 16px;
}

/* ========================================================== */

/* SECTION 2 - LINKS */

section.section-links {
  grid-column: 1 / 2;
  order: 1;
}

.section-links-container {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 8px;
}

.section-links-container a {
  text-decoration: none;
  color: #000;
}

/* =================================== */

/* EDUCATION + PERSONAL SKILLS + TECHNICAL SKILLS WRAPPER */

.wrapper-education-personal-and-technical-skills {
  max-width: 1006px;
  margin: 40px auto 0px auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 50px;
}

/* =================================== */

/* SECTION 3 - EDUCATION */

section.section-education p {
  font-size: 16px;
  line-height: 24px;
}

section.section-education p:first-of-type {
  font-weight: 600;
}

.education-wrapper {
  margin-top: 27px;
}

/* ===================================== */

/* SECTION 4 - PERSONAL SKILLS + SECTION TECHNICAL SKILLS */

.section-personalskills-buttons,
.section-technicalskills-buttons {
  color: #000;
  font-weight: 700;
  display: block;
  pointer-events: none;
  border: none;
  border-radius: 11px;
  margin-top: 30px;
}

.beginner-level {
  background: #7abcac;
}
.intermediate-level {
  background: #d8da79;
}
.advanced-level {
  background: #c29486;
}

.beginner-level,
.intermediate-level,
.advanced-level {
  display: inline-block;
  margin-right: 100%;
  line-height: 19px;
  font-size: 16px;
  border-radius: 11px;
  margin-bottom: 30px;
  padding: 0 10px;
  font-weight: 600;
}
/* ===================================== */

/* SECTION 5 - WORK EXPERIENCE */

.section-work-experience {
  max-width: 1006px;
  margin: auto;
  text-align: center;
  margin-top: 30px;
}

.work-experience-cards-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  justify-content: center;
  align-content: center;
  gap: 50px;
}

.section-work-experience-card:nth-of-type(2) {
  border-left: 2px #dcdcdc solid;
  padding-left: 20px;
  border-right: 2px #dcdcdc solid;
  padding-right: 15px;
}

.section-work-experience-card-title {
  text-align: center;
  margin: auto;
  font-size: 16px;
  font-family: 600;
}

.section-work-experience-card p {
  line-height: 25px;
  text-align: start;
}

.section-work-experience-card p:first-of-type,
.section-work-experience-card p:nth-of-type(2) {
  text-align: center;
}

.section-work-experience-card p:nth-of-type(2) {
  margin-bottom: 27px;
}

.section-work-experience-card ul > li {
  color: #000;
  text-align: left;
  margin-top: 8px;
}

/* ===================================== */

/* FOOTER */

footer {
  max-width: 991px;
  margin: auto;
  padding-bottom: 30px;
}

footer.footer-container {
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  max-width: 991px;
  gap: 20px;
}

footer a {
  text-decoration: none;
  color: #000;
}

footer a:hover,
a:active,
a:focus {
  color: #7abcac;
}

.footer-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  flex: 1;
}

.footer-card-header {
  background: #000;
  width: 100%;
  color: #fff;
  font-size: 24px;
  line-height: 28px;
  max-width: 248px;
  font-weight: 600;
}

/* 
MEDIA QUERIES  */

@media only screen and (max-width: 1240px) {
  .wrapper-education-personal-and-technical-skills > section:nth-of-type(3) {
    grid-column: 1 / 3;
  }
}

@media only screen and (max-width: 1160px) {
  .section-work-experience-card:nth-last-of-type(2) {
    border-left: none;
    border-right: none;
  }

  .section-work-experience-card {
    border: none;
    border-bottom: 2px #dcdcdc solid;
    padding-left: 0px;
    padding-right: 0px;
    padding-bottom: 20px;
  }
  .section-work-experience-card:nth-last-of-type(1) {
    grid-column: 1 / 3;
  }
}

@media only screen and (max-width: 1100px) {
  .wrapper-aboutMe-links {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr auto;
  }

  section.section-links {
    margin-bottom: 30px;
    max-width: 1006px;
    width: 100%;
  }

  section.section-aboutMe {
    max-width: 1006px;
    width: 100%;
    grid-column: 1;
    grid-row: 2;
  }
}

@media only screen and (max-width: 805px) {
  .wrapper-education-personal-and-technical-skills > section:nth-of-type(3) {
    grid-column: 1;
  }
}

@media only screen and (max-width: 752px) {
  .wrapper-education-personal-and-technical-skills {
    gap: 20px;
  }
  .section-work-experience-card:nth-last-of-type(1) {
    grid-column: 1;
  }
}

@media only screen and (max-width: 770px) {
  .section-work-experience-splitter {
    display: none;
  }
}

@media only screen and (max-width: 600px) {
  footer.footer-container {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 1fr);
  }
}

@media only screen and (max-width: 400px) {
  .header-name-container h2 {
    font-size: 38px;
  }
}
